
import { useContext, useState } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { AuthContext } from '../../context/auth-context';
import { IoMdBicycle } from "react-icons/io";
import Headercartbutton from '../../../cart/component/Layout/Headercartbutton';
import './NavLinks.css'

const NavLinks=(props)=> {
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);

  const auth= useContext(AuthContext)

  const openDrawerHandler = () => {
    setDrawerIsOpen(true);
  };

  const closeDrawerHandler = () => {
    setDrawerIsOpen(false);
  };

  return (
    <Navbar  collapseOnSelect expand="lg" bg="dark" variant="dark" className='navbar'>
      <Container>
        <Navbar.Brand href="/" className='brand_name' style={{color:"lightgreen", textShadow:"5px 5px 4px green"}}>GPS EV <IoMdBicycle className='brand_name_icon'/></Navbar.Brand>
       
        {/* cart  */}
        <div className='mob_cart'>
            {!auth.isLoggedIn  &&      
      <Nav.Link to={`/${auth.customerId}`} style={{marginTop:"5px"}}>
      <Headercartbutton onclick={props.onshowcart}  />
      </Nav.Link>
      } 
        </div>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mx-auto nav-link">
            <Nav.Link href="/" className='text-white' style={{marginLeft:"10px",textAling:"left"}}>Home</Nav.Link>
            {/* <Nav.Link href="#pricing" className='text-white'>Pricing</Nav.Link> */}
            <div style={{marginLeft:"10px", textAling:"left"}}>
            {!auth.isLoggedIn  && !auth.customerisLoggedIn &&
            <NavDropdown title="Admin" id="collasible-nav-dropdown" className='text-white'>
                  
      {!auth.isLoggedIn && !auth.customerName && (
              <NavDropdown.Item href="/sellerlogin">
              Admin Login   
              </NavDropdown.Item>
      )}
              
              {auth.isLoggedIn &&  (
        <li >
          <Nav.Link href="/sellerregistration" style={{color:"black", fontSize:"15px", marginLeft:"17px"}}>
          {auth.userName}
          </Nav.Link>
        </li>
      )}
                   </NavDropdown>
            }
            </div>
          </Nav>
          {/* <Nav> */}
              {!auth.customerisLoggedIn && !auth.isLoggedIn && (
            <li>
            <Nav.Link href="/customerregistration" className='text-white' style={{marginTop:"-14px", marginRight:"0px"}}>Login</Nav.Link>            
        </li>        
      )}
      {auth.customerisLoggedIn &&  (
            <li>
          <Nav.Link href="/customerregistration" style={{marginTop:"-10px", marginRight:"0px"}}>Hello {auth.customerName} </Nav.Link>
        </li>        
      )}      
      
      {auth.isLoggedIn &&  (
        <li>
          <Nav.Link href="/sellerregistration" style={{marginTop:"-10px", marginRight:"0px"}}>Hello {auth.userName}</Nav.Link>
        </li>
      )}

            {/* cart  */}
<div  className='cart1'>
{!auth.isLoggedIn  &&      
      <Nav.Link to={`/${auth.customerId}`} style={{marginTop:"5px", marginLeft:"0px"}}>
      <Headercartbutton onclick={props.onshowcart}  />
      </Nav.Link>
      } 
      </div>
      

                  {auth.isLoggedIn && (
        <li>
          <Nav.Link onClick={auth.logout} href="/" style={{marginTop:"-10px", marginRight:"-30px"}}>Logout</Nav.Link>
        </li>
      )}
      {auth.customerisLoggedIn && (
        <li>
          <Nav.Link onClick={auth.logout} href="/" style={{marginTop:"-10px", marginLeft:"0px"}}>Logout</Nav.Link>
        </li>
      )}

          {/* </Nav> */}
        </Navbar.Collapse>        
      </Container>
    </Navbar>
  );
}

export default NavLinks;
